import request from '@/utils/request'

/**
 * post
 * @param url
 * @param data
 * @returns {AxiosPromise}
 */

export function post(url, data) {
    console.log('url', data)
    return request({
        url: url,
        method: 'post',
        data
    })
}

export function get(url, data) {
    return request({
        url: url,
        method: 'get',
        data
    })
}

/**
 * get
 * @param url
 * @returns {AxiosPromise}
 */
export function getFile(url) {
    return request({
        url: url,
        method: 'get',
        responseType: 'arraybuffer'
    })
}

/**
 * 
 * @param baseUrl URL
 * @param data  
 * @returns {AxiosPromise}
 */
export function baseList(baseUrl, data) {
    return post(baseUrl + '/list', data)
}

/**
 * 
 * @param baseUrl URL
 * @param data  
 * @returns {AxiosPromise}
 */
export function baseJudge(baseUrl, data) {
    return post(baseUrl + '/judge', data)
}

/**
 * /
 * @param baseUrl URL
 * @param data  
 * @returns {AxiosPromise}
 */
export function baseEdit(baseUrl, data) {
    const { id } = data
    const url = baseUrl + (id === 0 ? '/add' : '/update')
    return post(url, data)
}

/**
 * 
 * @param baseUrl URL
 * @param ids id','
 * @returns {AxiosPromise}
 */
export function baseDel(baseUrl, ids) {
    const data = { ids: ids }
    return post(baseUrl + '/delete', data)
}
